import { useState } from "react";
import Alias from "./Alias";
import NotAlias from "./NotAlias";

export default function About() {
  const [freyrads, setFreyrads] = useState(false);

  const handleShowIdentityClick = () => {
    setFreyrads(!freyrads);
  }

  return (
    <div className="my-4 mx-10">
      <div className="flex justify-between">
        <span className="flex items-center">About Me ({freyrads ? "Fredy" : "freyrads"})</span>
        <button
          className="flex flex-row gap-2 text-red-400 hover:bg-red-400 hover:text-white py-2 px-4 rounded print:hidden transition-colors duration:300"
          onClick={handleShowIdentityClick}
        >
          <span>
              {/* xmlns:xlink="http://www.w3.org/1999/xlink" */}
            <svg
              xmlns="http://www.w3.org/2000/svg"
              aria-hidden="true"
              role="img"
              className="h-6 w-6 print:hidden"
              preserveAspectRatio="xMidYMid meet"
              viewBox="0 0 24 24">
              <path
                fill="currentColor"
                d="M12 6.5a9.77 9.77 0 0 1 8.82 5.5c-1.65 3.37-5.02 5.5-8.82 5.5S4.83 15.37 3.18 12A9.77 9.77 0 0 1 12 6.5m0-2C7 4.5 2.73 7.61 1 12c1.73 4.39 6 7.5 11 7.5s9.27-3.11 11-7.5c-1.73-4.39-6-7.5-11-7.5zm0 5a2.5 2.5 0 0 1 0 5a2.5 2.5 0 0 1 0-5m0-2c-2.48 0-4.5 2.02-4.5 4.5s2.02 4.5 4.5 4.5s4.5-2.02 4.5-4.5s-2.02-4.5-4.5-4.5z">
              </path>
            </svg>
          </span>
          <span>Show {freyrads ? "Alias" : "Identity"}</span>
        </button>
      </div>
      {
        freyrads ? <NotAlias /> : <Alias />
      }
    </div>
  );
}
