import {
  createBrowserRouter,
} from "react-router-dom";
import App from "../App";
import About from "../views/About";
import Home from "../views/Home";
import Portofolio from "../views/Portofolio";
import Project from "../views/Project";

export default createBrowserRouter([
  {
    path: "/",
    element: <App />,
    children: [
      {
        path: "",
        element: <Home />,
      },
      {
        path: "resume",
        element: <About />,
      },
      {
        path: "portofolio",
        element: <Portofolio />,
      },
      {
        path: "project/:id",
        element: <Project />,
      },
    ]
  },
]);
