import { Link } from "react-router-dom";

export default function ProjectCard({ data, index }) {
  const rowNum = index && index / 2;

  return (
    <div style={{
      grid: index && index % 2 ? 2 : 1,
      gridTemplate: rowNum ? rowNum : 1,
    }} className="project-card">
      <Link className="hero" to={ "/project/" + index}>
        <img src={data.baseScreenshotPath + data.image} alt="Project Screenshot" />
        <div className="project-overlay">
          <h1 className="text-5xl font-bold project-card-title">{data.name}</h1>
        </div>
      </Link>
      <p className="m-4">
        {data.desc}
      </p>
      <Link className="btn btn-outline btn-success font-bold" to={ "/project/" + index}>
        VIEW SCREENSHOTS
      </Link>
    </div>
  );
}
