export const addChar = (char, ref, timeout) => {
  setTimeout(() => {
    if (ref.current)
      ref.current.innerText += char;
  }, timeout);
}

export const typingEffect = (ref, text) => {
  if (ref.current) {
    const innerText = text ?? ref.current.innerText;

    ref.current.innerText = "";

    let timeout = 0;

    for (let i = 0; i < innerText.length; i++) {
      let char = innerText[i];

      let randTime = Math.floor(Math.random() * 51 + 10);

      while (innerText[i+1] && char[char.length - 1] === " ") {
        char += innerText[++i];
        randTime += 50;
      }

      timeout += randTime;
      
      addChar(char, ref, timeout);

      if (char === '.' && innerText[i+1] === " ") timeout += 500;
    }
  }
}
